import { Component, OnInit, ViewChild, ElementRef } from "@angular/core";
import { TwilioserviceService } from "../twilioservice.service";
import { Router, ActivatedRoute } from "@angular/router";

@Component({
  selector: "app-room",
  templateUrl: "./room.component.html",
  styleUrls: ["./room.component.scss"],
})
export class RoomComponent implements OnInit {
  @ViewChild("local") localVideo: ElementRef;
  @ViewChild("remote") remoteVideo: ElementRef;
  roomName: string;
  dateTime: string;
  accessToken: any;
  constructor(
    private twilioService: TwilioserviceService,
    private route: ActivatedRoute,
    private router: Router
  ) {
    this.route.queryParams.subscribe((params) => {
      this.roomName = params["roomName"];
      this.dateTime = params["dateTime"];
    });
    this.accessToken = localStorage.getItem("accessToken");
  }

  ngOnInit() {
    this.twilioService.localVideo = this.localVideo;
    this.twilioService.remoteVideo = this.remoteVideo;
    this.twilioService.connectToRoom(this.accessToken, {
      name: this.roomName,
      audio: true,
      video: { width: 240 },
    });
  }
  disconnect() {
    this.twilioService.roomObj.disconnect();
    this.router.navigate(["dashboard"]);
  }
}
