import { Component, OnInit } from "@angular/core";
import { UserService } from "../user.service";
import { UserInterface } from "./userData";
import { TwilioserviceService } from "../twilioservice.service";
import { Router } from "@angular/router";

@Component({
  selector: "app-mymeeting",
  templateUrl: "./mymeeting.component.html",
  styleUrls: ["./mymeeting.component.scss"],
})
export class MymeetingComponent implements OnInit {
  constructor(
    private userservice: UserService,
    private twilioservice: TwilioserviceService,
    private router: Router
  ) {}
  data: UserInterface;
  ngOnInit() {
    this.userservice.getData(localStorage.getItem("userId")).subscribe((e) => {
      this.data = e;
    });
  }

  join(dateTime, roomname) {
    this.twilioservice
      .getAccessToken(dateTime, this.data.username, roomname)
      .subscribe(
        (result) => {
          localStorage.setItem("accessToken", result.token);
          this.router.navigate(["room"], {
            queryParams: {
              roomName: roomname,
              dateTime: dateTime,
            },
          });
        },
        (error) => {
          console.log(error);
        }
      );
  }
}
