import { Injectable } from "@angular/core";
import { UserDetailsApi } from "../../sdk/services/custom/UserDetails";
import { HttpClient } from "@angular/common/http";
import { UserInterface } from "./mymeeting/userData";
import { Observable } from "rxjs";
import { environment } from "../environments/environment";
@Injectable({
  providedIn: "root",
})
export class UserService {
  constructor(
    private UserDetailsApi: UserDetailsApi,
    private http: HttpClient
  ) {}

  invite(data) {
    return this.UserDetailsApi.invite(data);
  }

  getData(userId): Observable<UserInterface> {
    return this.http.get<UserInterface>(
      `${environment.api_url}/api//userDetails/${userId}`
    );
  }
}
