import { Component, OnInit } from "@angular/core";
import { UserService } from "../user.service";
import { MatSnackBar } from "@angular/material/snack-bar";

@Component({
  selector: "app-setmeeting",
  templateUrl: "./setmeeting.component.html",
  styleUrls: ["./setmeeting.component.scss"],
})
export class SetmeetingComponent implements OnInit {
  constructor(
    private userservice: UserService,
    private _snackBar: MatSnackBar
  ) {}
  datetime: any;
  rname: string;
  remail: string;
  ngOnInit() {}
  submit() {
    console.log("Submit clicked");
    let data = {
      userId: localStorage.getItem("userId"),
      recieverEmail: this.remail,
      recieverName: this.rname,
      dateTime: this.datetime,
    };
    this.userservice.invite(data).subscribe(
      (data) => {
        this._snackBar.open("Invite Sent sucessfully", "Okay", {
          duration: 2000,
        });
        this.datetime = "";
        this.rname = "";
        this.remail = "";
        console.log(data);
        console.log("invite sent successfully");
      },
      (error) => {
        console.log(error);
      }
    );
  }
}
