import { Component, OnInit } from "@angular/core";
import { LoginService } from "../login.service";
import { TwilioserviceService } from "../twilioservice.service";
import { Router, ActivatedRoute } from "@angular/router";

@Component({
  selector: "app-maillogin",
  templateUrl: "./maillogin.component.html",
  styleUrls: ["./maillogin.component.scss"],
})
export class MailloginComponent implements OnInit {
  email: string;
  username: string;
  roomName: string;
  dateTime: string;

  constructor(
    private loginservice: LoginService,
    private twilioService: TwilioserviceService,
    private route: ActivatedRoute,
    private router: Router
  ) {
    this.route.queryParams.subscribe((params) => {
      this.roomName = params["roomName"];
      this.dateTime = params["dateTime"];
    });
  }
  ngOnInit() {}
  submit() {
    this.loginservice.login(this.username, this.email).subscribe((result) => {
      this.twilioService
        .getAccessToken(this.dateTime, this.username, this.roomName)
        .subscribe(
          (result) => {
            localStorage.setItem("accessToken", result.token);
            this.router.navigate(["room"], {
              queryParams: {
                roomName: this.roomName,
                dateTime: this.dateTime,
              },
            });
          },
          (error) => {
            console.log(error);
          }
        );
    });
  }
}
