import { Injectable } from "@angular/core";
import { UserDetailsApi } from "../../sdk/services/custom/UserDetails";
@Injectable({
  providedIn: "root",
})
export class LoginService {
  constructor(private UserDetailsApi: UserDetailsApi) {}

  login(username, email) {
    return this.UserDetailsApi.login({
      username: username,
      email: email,
      password: "pass@123",
    });
  }
}
